@import '../../../variable.less';

.whatWeBuildSection2 {
  background: #1346ff;
  color: #fff;

  .content {
    max-width: @max-content-width;
    margin: 0 auto;
    position: relative;

    .bottomAds {
      text-align: center;
      font-size: 0.3rem;
      font-family: Gilroy-Bold, Gilroy;
      font-weight: bold;
      color: #fff;
      line-height: 1;
      padding: 0 0.7rem;
      position: relative;

      .comment {
        transform: rotate(60deg);
        width: 1rem;
        height: 1rem;
        position: absolute;
        right: 0.2rem;
        top: -0.4rem;

        & > div {
          background: #9fd2ff;
          height: 100%;
          width: 100%;
        }
      }

      .itemImg {
        height: 1.1rem;
        margin-bottom: 0.4rem;
      }
      .secondRow {
        margin-top: 0.8rem;
      }
      .adItem {
        padding: 0.8rem 0.6rem 0.6rem;
        border-radius: 0.2rem;
        height: 100%;
      }
    }
    .details {
      margin-top: 1.2rem;
      margin-bottom: 1.1rem;
      text-align: center;
      .detailRow {
        align-items: stretch;
      }
      .detailItem {
        // background: rgba(255, 255, 255, 0.06);
        padding: 0.6rem 0.2rem;
        height: 100%;

        &.active {
          // background: rgba(255, 255, 255, 0.15);
        }
        .itemImg {
          width: 0.6rem;
          height: 0.6rem;
          margin: 0 auto;
        }
        p {
          font-size: 0.18rem;
          font-family: Gilroy-Regular, Gilroy;
          font-weight: 400;
          color: #ffffff;
          line-height: 0.24rem;
          margin-top: 0.4rem;
          margin-bottom: 0;
        }
      }
    }
    .partnerWpr {
      text-align: center;
    }

    .partnerItem {
      width: 1.2rem;
      height: 0.5rem;
      display: inline-block;
      margin: 0.35rem 0;
    }

    img {
      height: 100%;
    }
  }
}

