@import '../../../variable.less';

.asFeature {
  height: 50vh;
  display: flex;
  align-items: center;
  justify-content: center;

  .featureContent {
    padding: 0;
    margin: 0 auto;
    background: #fafafa;
    text-align: center;
    width: 100%;
    padding: 0.4rem 0;
  }
  h2 {
    font-size: 0.6rem;
    font-family: Gilroy-Bold, Gilroy;
    font-weight: bold;
    color: #000000;
    line-height: 0.74rem;
    text-align: center;
    margin: 0;
  }

  .desc {
    font-size: 0.3rem;
    font-family: Gilroy-Bold, Gilroy;
    font-weight: bold;
    color: #000000;
    line-height: 0.37rem;
    margin-bottom: 0.2rem;
  }

  a.link {
    font-size: 0.6rem;
    font-family: Helvetica;
    color: #000000;
    line-height: 0.72rem;
  }

  .partnerItem {
    width: 1.2rem;
    height: 0.5rem;
    display: inline-block;
    margin: 0.6rem 0 0.4rem;
  }

  img {
    height: 100%;
  }
}

